import { Component, OnInit ,ViewChild } from '@angular/core';
import { ApiService } from '../common/api.service';
import { Observable } from 'rxjs/Observable';

import {
  Ng2OverlayManager,
  Ng2PopupComponent,
  Ng2MessagePopupComponent,
  Ng2ScrollableDirective
} from 'ng2-ui';
@Component({
  selector: 'app-clients',
  templateUrl: './clients.component.html'
})
export class ClientsComponent implements OnInit {

  @ViewChild(Ng2PopupComponent) popup: Ng2PopupComponent;

   clients:any;


   constructor(private apiService: ApiService) {


  }


  ngOnInit() {

    this.apiService.getClients().subscribe(
                                    (res) => {
                                      console.log(res);
                                      this.clients = res._embedded.clients;
                                    },
                                    (err) => {
                                        console.log(err);
                                    });
  }


  deleteClient(id) {

    this.apiService.delete('clients',id).subscribe(
                                    (res) => {
                                      console.log(res);
                                      for ( var i = 0 ; i < this.clients.length ; i++ ) {
                                        console.log(this.clients[i]);
                                        if (!!this.clients[i] && this.clients[i].id == id) {
                                          this.clients.splice(i);
                                        }
                                      }
                                    },
                                    (err) => { console.log(err); });

/*    this.popup.open(Ng2MessagePopupComponent, {
     //classNames: size,
     title: 'Delete Client',
     message: "Are you sure you want to delete the client ?",
     buttons: {
       OK: () => {


       },
       CANCEL: () => {
         this.popup.close();
       }
     }
   });*/


  }


}



