import { Component, OnInit , ViewChild } from '@angular/core';
import {Router} from '@angular/router';
import {
  Ng2OverlayManager,
  Ng2PopupComponent,
  Ng2MessagePopupComponent,
  Ng2ScrollableDirective
} from 'ng2-ui';
import {Location} from '../../models/location';
import { ApiService } from '../../common/api.service';

@Component({
  selector: 'app-add-location',
  templateUrl: './add-location.component.html'
})
export class AddLocationComponent implements OnInit {

  @ViewChild(Ng2PopupComponent) popup: Ng2PopupComponent;
  currLocation :Location;
  pos;
  map;
  constructor(private apiService: ApiService,private router: Router) {

    this.currLocation = new Location();
    this.pos = [43.72,-79.7699];
    console.log(this.router);
  }

  dragEnd(event) {

    if ( !!event.latLng) {
      this.pos = (event.latLng);
      this.currLocation.latitude = this.pos.lat();
      this.currLocation.longitude = this.pos.lng();
      event.target.ng2MapComponent.map.panTo(event.latLng);
    }


  }

  changeMarker(event) {

    if (event instanceof MouseEvent)
       return;
   this.pos = (event.latLng);
   this.currLocation.latitude = this.pos.lat();
   this.currLocation.longitude = this.pos.lng();
   event.target.panTo(event.latLng);
  }
  submit() {
      var clientId = this.apiService.currTempId;
      this.currLocation.clientId = clientId;
      console.log('submit called');
      this.apiService.addLocation(this.currLocation).subscribe(
                                      (res) => {
                                        this.currLocation = res;
                                        console.log(this.currLocation);
                                        this.router.navigate(['/clients/edit/'+clientId], { queryParams: { location: this.currLocation.id } });
                                        //this.router.navigateByUrl('/clients/edit/'+clientId);
                                        this.popup.close();

                                      },
                                      (err) => {
                                          console.log(err);
                                      });



  }
  ngOnInit() {



  }

  closePopUp() {
    this.popup.close();
  }

}



