declare var moment: any;
import { RouterModule } from '@angular/router';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { Ng2UIModule }  from 'ng2-ui';
import { Ng2MapModule }  from 'ng2-map';
import { Ng2DatetimePickerModule } from 'ng2-datetime-picker';
import { AlertModule ,ModalModule ,TabsModule } from 'ng2-bootstrap';

import { clientsRouting } from "./clients/clients.routes";
import { scheduleRouting } from "./schedule/schedule.routes";
import { guardsRouting } from "./guards/guards.routes";
import { routing } from "./app.routes";

import { AppComponent } from './app.component';
import { UserComponent } from './user/user.component';
import { AddGuardComponent } from './guards/addguard/addguard.component';
import { GuardsComponent } from './guards/guards.component';
import { ClientsComponent } from './clients/clients.component';
import { AddClientComponent } from './clients/add-client/add-client.component';
import { AddLocationComponent } from './clients/add-location/add-location.component';
import { ScheduleComponent } from './schedule/schedule.component';
import { AddDutyComponent } from './schedule/add-duty/add-duty.component';
import { ScheduleDetailComponent } from './schedule/schedule-detail/schedule-detail.component';
import { TimesheetComponent } from './schedule/timesheet/timesheet.component';
import { IncidentComponent } from './incident/incident.component';
import { AlertComponent } from './alert/alert.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { ReportsComponent } from './reports/reports.component';
import { AlertDetailComponent } from './alert/alert-detail/alert-detail.component';
import { IncidentDetailComponent } from './incident/incident-detail/incident-detail.component';

import { ApiService } from './common/api.service';

import { ClientsResolver } from './common/resolvers/clients.resolver';
import { UsersResolver } from './common/resolvers/users.resolver';
import { DutiesResolver } from './common/resolvers/duties.resolver';
import { TimesheetsResolver } from './common/resolvers/timesheets.resolver';

@NgModule({
  declarations: [
    AppComponent,
    UserComponent,
    AddGuardComponent,
    GuardsComponent,
    ClientsComponent,
    AddClientComponent,
    AddLocationComponent,
    ScheduleComponent,
    AddDutyComponent,
    ScheduleDetailComponent,
    TimesheetComponent,
    IncidentComponent,
    AlertComponent,
    DashboardComponent,
    ReportsComponent,
    AlertDetailComponent,
    IncidentDetailComponent,

  ],
  imports: [
    scheduleRouting,
    clientsRouting,
    guardsRouting,
    routing,
    BrowserModule,
    FormsModule,
    HttpModule,
    Ng2UIModule,
    Ng2DatetimePickerModule,
    TabsModule.forRoot(),
    ModalModule.forRoot(),
    Ng2MapModule.forRoot({apiUrl: 'https://maps.google.com/maps/api/js?libraries=visualization,places,drawing&key=AIzaSyDbJZtwWtcv3_LLOFaPCNkndh_4_hEFVhc'})
  ],
  providers: [ApiService , ClientsResolver , UsersResolver , DutiesResolver , TimesheetsResolver],
  bootstrap: [AppComponent ],
  entryComponents: [AddLocationComponent]
})
export class AppModule { }



