import { Injectable }     from '@angular/core';
import { Http, Response, Headers, RequestOptions } from '@angular/http';
import {Observable} from 'rxjs/Rx';
import {Client} from '../models/client'
// Import RxJs required methods
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/catch';

@Injectable()
export class ApiService {

  public baseUrl = 'http://54.190.51.6:3307/';
  public baseApiUrl = 'http://54.190.51.6:3307/api/';
  private commentsUrl = 'http://localhost:3000/api/comments';
  private options;

  public currTempId;
  public recentlyAddedLocation;
  public currTempLocId;

     // Resolve HTTP using the constructor
     constructor (private http: Http) {

       let headers = new Headers();
       headers.append('content-type','application/json');
       this.options = new RequestOptions({
           headers: headers,
           withCredentials: true
       });

     }


     login(user,pass) {

            var params = "username="+user+"&password="+pass;
            let headers = new Headers();
            headers.append('content-type','application/x-www-form-urlencoded');
            let options = new RequestOptions({
                headers: headers,
                withCredentials: true
            });

            // ...using get request
            return this.http.post(this.baseUrl+'login',params,options)
                           // ...and calling .json() on the response to return data
                            .map((res:Response) => res.json())
                            //...errors if any
                            .catch((error:any) => Observable.throw(error || 'Server error'));

      }


      getClients(id?) {

        // ...using get request
        return this.http.get(this.baseApiUrl + (!!id ? 'clients/'+id : 'clients/search/findMyClients'),this.options)
                       // ...and calling .json() on the response to return data
                        .map((res:Response) => res.json())
                        //...errors if any
                        .catch((error:any) => Observable.throw(error || 'Server error'));


      }


      getLocations(clientId) {
        //!!id ? 'clients/'+id :
        // ...using get request
        return this.http.get(this.baseApiUrl + ( 'clientLocations/search/findByClientId?clientID='+clientId),this.options)
                       // ...and calling .json() on the response to return data
                        .map((res:Response) => res.json())
                        //...errors if any
                        .catch((error:any) => Observable.throw(error || 'Server error'));


      }

      getUsers(id?) {
        //!!id ? 'clients/'+id :
        // ...using get request
        return this.http.get(this.baseApiUrl + (!!id ? 'users/'+id : 'users'),this.options)
                       // ...and calling .json() on the response to return data
                        .map((res:Response) => res.json())

                        //...errors if any
                        .catch((error:any) => Observable.throw(error || 'Server error'));


      }



      add(type,obj) {


        return this.http.post(this.baseApiUrl+type,obj,this.options)
                        .map((res:Response) => res.json())
                        .catch((error:any) => Observable.throw(error || 'Server error'));


      }


      addLocation(location) {

        return this.http.post(this.baseApiUrl+'addclientlocation',location,this.options)
                        .map((res:Response) => res.json())
                        .catch((error:any) => Observable.throw(error || 'Server error'));


      }


      delete(type,id) {

        return this.http.delete(this.baseApiUrl+type+'/'+id,this.options)
                        .map((res:Response) => res.json())
                        .catch((error:any) => Observable.throw(error || 'Server error'));


      }

      getDuties(id?) {

        return this.http.get(this.baseApiUrl + 'dutySchedules',this.options)
                        .map((res:Response) => res.json()._embedded.dutySchedules)
                        .catch((error:any) => Observable.throw(error || 'Server error'));


      }

      getTimesheets(id?) {

        return this.http.get(this.baseApiUrl + 'duties',this.options)
                        .map((res:Response) => res.json()._embedded.duties)
                        .catch((error:any) => Observable.throw(error || 'Server error'));


      }



}



