import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router'
import { ApiService } from '../../common/api.service';
import {User} from '../../models/user';
@Component({
  selector: 'app-addguard',
  templateUrl: './addguard.component.html'
})
export class AddGuardComponent implements OnInit {


  id;
  currUser: User;
  constructor(private _route: ActivatedRoute,private apiService: ApiService) {


      this.currUser = new User();

  }

  ngOnInit() {
    this._route.params.subscribe(params => {
      this.id = +params["id"]
      if ( !!this.id ) {

        this.apiService.getUsers(this.id).subscribe(
                                        (res) => {
                                          this.currUser = res;
                                          console.log(this.currUser,'user');
                                        },
                                        (err) => {
                                            console.log(err);
                                        });

      }
    });
  }

  submit() {

    this.currUser.userType = 'GUARD';
    this.apiService.add('users',this.currUser).subscribe(
                                    (res) => {
                                      this.currUser = res;
                                      console.log(this.currUser);

                                    },
                                    (err) => {
                                        console.log(err);
                                    });


  }



}



