import { Router , RouterModule } from '@angular/router';

import { ScheduleComponent } from './schedule.component';
import { AddDutyComponent } from './add-duty/add-duty.component';
import { ScheduleDetailComponent } from './schedule-detail/schedule-detail.component';
import { TimesheetComponent } from './timesheet/timesheet.component';
import { ClientsResolver } from '../common/resolvers/clients.resolver';
import { UsersResolver } from '../common/resolvers/users.resolver';
import { DutiesResolver } from '../common/resolvers/duties.resolver';
import { TimesheetsResolver } from '../common/resolvers/timesheets.resolver';

export const scheduleRouting = RouterModule.forChild([
  { path: 'schedule/add' , component: AddDutyComponent ,  resolve: {  clients: ClientsResolver , users: UsersResolver } },
  { path: 'schedule/edit/:id' , component: AddDutyComponent},
  { path: 'schedule/detail/:id' , component: ScheduleDetailComponent},
  { path: 'schedule/detail/:id/timesheet' , component: TimesheetComponent , resolve: {  timesheets: TimesheetsResolver } },
  // { path: 'guards/edit/:id' , component: AddGuardComponent},
]);



