export class Duty {

guardId;
guard;
clientId;
client;
clientLocationId;
clientLocation;
startTime;
endTime;
allowedBreakHrs;
date;

Duty(obj?) {

    if ( !!obj)
      for (var prop in obj)
        this[prop] = obj[prop];
}


}



