import { Component, OnInit ,ViewChild } from '@angular/core';
import {
  Ng2OverlayManager,
  Ng2PopupComponent,
  Ng2MessagePopupComponent,
  Ng2ScrollableDirective
} from 'ng2-ui';
import { AddLocationComponent } from '../add-location/add-location.component';
import { ActivatedRoute } from '@angular/router';
import {Client} from '../../models/client';
import { ApiService } from '../../common/api.service';

@Component({
  selector: 'app-add-client',
  templateUrl: './add-client.component.html'
})
export class AddClientComponent implements OnInit {

  @ViewChild(Ng2PopupComponent) popup: Ng2PopupComponent;
  id;
  currClient :Client;
  locations;
  lastEnteredLocation;
  constructor(private _route: ActivatedRoute , private apiService: ApiService) {
      this.currClient = new Client();

  }
  loadClients() {
    this.apiService.getClients(this.id).subscribe(
                                    (res) => {
                                      this.currClient = res;
                                      console.log(this.currClient);
                                      this.apiService.getLocations(this.currClient.id).subscribe(
                                                                      (locations) => {
                                                                        this.locations = locations._embedded.clientLocations;
                                                                        console.log('locations',locations);
                                                                      });
                                    },
                                    (err) => {
                                        console.log(err);
                                    });
  }
  ngOnInit() {

      this._route.queryParams.subscribe(queryParams => {

        if ( this.lastEnteredLocation != queryParams['location']) {
          this.lastEnteredLocation = queryParams['location'];
          this.loadClients();
        }


      });

    this._route.params.subscribe(params => {

      this.id = +params["id"];
      if ( !!this.id ) {
        this.loadClients();
      }

  });
}

  submit() {

    this.apiService.add('clients',this.currClient).subscribe(
                                    (res) => {
                                      this.currClient = res;
                                      console.log(this.currClient);

                                    },
                                    (err) => {
                                        console.log(err);
                                    })


  }

  openPopup(locationId) {
    if ( !!locationId) {
      this.apiService.currTempId = this.currClient.id;
      this.popup.open(AddLocationComponent, {
        closeWindow : true,
        clientId : this.currClient.id,
        classNames : 'large location-popup'
      });
    } else {
      this.apiService.currTempLocId = locationId;
      this.popup.open(AddLocationComponent, {
        closeWindow : true,
        clientId : this.currClient.id,
        classNames : 'large location-popup'
      });
    }

  }
}



